.home {
	height: 100vh;
}

header {
	padding-top: 5%;
	position: relative;

	.skipLink {
		position: absolute;
		left: -1000px;
		top: 2rem;
		z-index: 999;
		color: $primaryFontColor;
		border: 0.4rem solid $primaryFontColor;
		padding: 0.5rem 1rem;
	}

	.skipLink:focus {
		left: 0;
	}

	nav {
		float: right;
		width: max(35%, 35rem);
		position: relative;

		ul a:hover::after {
			content: "";
			width: 23%;
			height: 0.25rem;
			background: $primaryFontColor;
			position: absolute;
			bottom: 20%;
		}
	}

	.mainHeading {
		padding-top: clamp(25rem, 28.0612rem + -10.2041vw, 15rem);
	}
}
.about {
	display: flex;
	justify-content: space-evenly;
	flex-wrap: wrap;
	align-content: center;
	height: 100vh;
	position: relative;

	.headshot {
		display: flex;
		justify-content: center;
		height: clamp(20rem, 15.4082rem + 15.3061vw, 35rem);

		img {
			width: clamp(12rem, 9.551rem + 8.1633vw, 20rem);
			box-shadow: 0 10px 50px 5px $primaryFontColor;
		}
	}

	.bio {
		display: flex;
		flex-direction: column;
		align-items: center;
		width: clamp(19.4rem, 9.4694rem + 33.102vw, 51.84rem);
	}
}

.coreCompetencies {
	display: flex;
	flex-direction: column;
	justify-content: center;
	height: 100vh;
	position: relative;

	li {
		display: flex;
		align-items: center;
		text-align: center;
		flex-direction: column;
		flex-wrap: wrap;
		width: clamp(8rem, 5.8571rem + 7.1429vw, 15rem);
		padding-bottom: 2rem;
	}
	ul {
		img {
			display: flex;
			width: clamp(2.5rem, 2.3469rem + 0.5102vw, 3rem);
		}
	}

	ul img:hover {
		animation: spin 2s infinite;
		animation-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);

		@keyframes spin {
			0% {
				transform: rotate(0deg);
			}
			100% {
				transform: rotate(360deg);
			}
		}
	}
}

.projects {
	padding: 2.5% 0;
}

.projects,
.portfolioProjects {
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-content: center;

	.container {
		padding-bottom: 2.5%;
	}

	.card {
		display: flex;
		flex-direction: column;
		position: relative;
		width: clamp(21.87rem, 19.0445rem + 9.4184vw, 31.1rem);
		height: clamp(25rem, 23.4694rem + 5.102vw, 30rem);
		margin-bottom: 2rem;

		h4 {
			margin: 1.5% 0;
		}
	}

	.stockOMeter,
	.quickFlickPix,
	.rainOrShine {
		position: relative;
		overflow-x: hidden;
	}

	.stockOMeter:hover::before {
		background-color: black;
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-image: url(../assets/dollar.jpg);
		filter: opacity(0.5);
		animation: moneyDrop 2s ease infinite;
	}

	@keyframes moneyDrop {
		0% {
			background-position: 20% 100%;
		}

		100% {
			background-position: 0 0;
		}
	}

	.quickFlickPix:hover::before {
		content: "";
		position: absolute;
		width: 329%;
		height: 100%;
		background-image: url(../assets/filmStrip.png);
		filter: opacity(0.3);
		animation: filmRoll 4s linear infinite;
	}

	@keyframes filmRoll {
		0% {
			transform: translateX(-100%);
		}

		100% {
			transform: translateX(100%);
		}
	}

	.rainOrShine:hover::before {
		background-color: black;
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-image: url(../assets/rain.png);
		filter: opacity(0.6);
		animation: rainAnimation 0.45s linear infinite;
	}

	@keyframes rainAnimation {
		0% {
			background-position: 20% 100%;
		}

		100% {
			background-position: 0 0;
		}
	}
}

.codeLinks {
	display: flex;
	justify-content: space-around;
	position: absolute;
	bottom: 0;
	width: clamp(20rem, 16.9388rem + 10.2041vw, 30rem);

	a {
		display: flex;
		align-items: center;
		justify-content: center;
	}

	a:hover,
	a:focus {
		outline: 2px solid $primaryFontColor;
		border-radius: 2.5%;
	}

	img {
		margin-right: 2%;
		padding: 2%;
		width: 2.5rem;
		height: 2.5rem;
	}

	img:hover {
		animation: spin 2s infinite;
		animation-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);

		@keyframes spin {
			0% {
				transform: rotate(0deg);
			}
			100% {
				transform: rotate(360deg);
			}
		}
	}
}

.portfolioLink {
	display: flex;
	position: relative;

	a:hover::after {
		content: "";
		width: 18.525rem;
		height: 0.25rem;
		background: $primaryFontColor;
		position: absolute;
		bottom: 20%;
	}
}

.personalInfo {
	.personalInfoContainer {
		display: flex;
		justify-content: center;
		padding-bottom: 2%;
	}

	ul {
		width: 70%;
		display: flex;
		justify-content: space-around;
		align-items: center;
	}

	.socialMedia {
		width: 30%;
		display: flex;
		justify-content: space-around;
	}

	li,
	a {
		display: flex;
		align-items: center;
	}

	img {
		width: 2.5rem;
	}

	p {
		margin-left: 5%;
	}

	img:hover {
		animation: spin 2s infinite;
		animation-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);

		@keyframes spin {
			0% {
				transform: rotate(0deg);
			}
			100% {
				transform: rotate(360deg);
			}
		}
	}
}

.returnToTop {
	display: flex;
	justify-content: center;
	padding-bottom: 2.5%;

	a {
		padding: 1%;
		border: 2px solid $primaryFontColor;
		box-shadow: 0 1px 80px 5px $primaryFontColor;
	}

	a:hover,
	a:focus {
		background-color: $primaryFontColor;
		color: white;
	}
}

@media screen and (min-width: 901px) {
	.dropdown main {
		display: none;
	}
}

@media screen and (max-width: 900px) {
	.dropdown main {
		display: inline-block;
	}

	.navContainer main ul {
		display: none;
	}
}

@media screen and (max-width: 650px) {
	.container {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.projects {
		padding-bottom: 20%;
	}

	.coreCompetencies,
	.projects,
	.portfolioProjects {
		h2 {
			display: flex;
			justify-content: center;
		}
	}

	.projects,
	.portfolioProjects {
		text-align: center;
	}

	.portfolioLink {
		justify-content: center;
	}

	.personalInfo {
		ul {
			flex-direction: column;
			width: 100%;
		}
	}

	.socialMedia,
	.returnToTop {
		display: flex;
		justify-content: center;
	}

	.socialMedia,
	.returnToTop {
		padding-bottom: 10%;
	}
}

@media screen and (max-width: 549px) {
	.personalInfo {
		ul {
			flex-direction: column;
		}
		li {
			padding-bottom: 2.5%;
		}
	}

	.socialMedia {
		display: flex;
		justify-content: center;
	}
}

@media screen and (max-width: 450px) {
	.skipLink {
		display: none;
	}
}
