.contact {
	height: clamp(10rem, 5.4082rem + 15.3061vw, 25rem);
}


.contactForm {
	display: flex;
	justify-content: center;
	flex-direction: column;
	padding-bottom: 2.5%;

	form {
		display: flex;
		flex-direction: column;
		width: clamp(25rem, 12.1429rem + 42.8571vw, 67rem);

		label,
		input,
		textarea {
			margin-bottom: 1rem;
		}

		input {
			width: clamp(15rem, 11.9388rem + 10.2041vw, 25rem);
		}

		textarea {
			width: clamp(25rem, 14.2857rem + 35.7143vw, 60rem);
			resize: none;
		}

		button {
			width: min(20%, 8rem);
            padding: 1%;
		}

		button:hover,
		button:focus {
			background-color: $primaryFontColor;
			color: white;
			cursor: pointer;
		}
	}
}

/* The Modal (background) */
.modal {
	display: none;
	position: fixed;
	z-index: 1;
	padding-top: 100px;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	overflow: auto;
	background-color: $primaryFontColor;
	background-color: rgba(0, 0, 0, 0.4);
}

/* Modal Content */
.modalContent {
	background-color: #fefefe;
	position: relative;
	color: $primaryFontColor;
	margin: auto;
	padding: 20px;
	border: 1px solid #888;
	width: max(33%, 25rem);
}

/* The Close Button */
.close {
	color: #aaaaaa;
	position: absolute;
	right: 2%;
	top: 2%;
	font-size: 28px;
	font-weight: bold;
}

.close:hover,
.close:focus {
	color: #000;
	text-decoration: none;
	cursor: pointer;
}
