.portfolio {
    height: clamp(13.5rem, -0.9444rem + 22.2222vw, 27.5rem);
}

.portfolioProjects {

	.card {
		margin-bottom: 5%;
	}

    .stockOMeter,
	.quickFlickPix,
	.rainOrShine {
		position: relative;
		overflow-x: hidden;
	}

	.stockOMeter:hover::before {
		background-color: black;
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-image: url(../assets/dollar.jpg);
		filter: opacity(0.5);
		animation: moneyDrop 2s ease infinite;
	}

	@keyframes moneyDrop {
		0% {
			background-position: 20% 100%;
		}

		100% {
			background-position: 0 0;
		}
	}

	.quickFlickPix:hover::before {
		content: "";
		position: absolute;
		width: 329%;
		height: 100%;
		background-image: url(../assets/filmStrip.png);
		filter: opacity(0.3);
		animation: filmRoll 4s linear infinite;
	}

	@keyframes filmRoll {
		0% {
			transform: translateX(-100%);
		}

		100% {
			transform: translateX(100%);
		}
	}

	.rainOrShine:hover::before {
		background-color: black;
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-image: url(../assets/rain.png);
		filter: opacity(0.6);
		animation: rainAnimation 0.45s linear infinite;
	}

	@keyframes rainAnimation {
		0% {
			background-position: 20% 100%;
		}

		100% {
			background-position: 0 0;
		}
	}

    .knowledgeHub:hover::before {
        content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 20%;
		background-image: url(../assets/questionMarks.jpg);
        background-position: center;
        background-size: cover;
		filter: opacity(0.2);
		animation: questions 2s cubic-bezier(0.5, 0.7, 0.5, 0.1) infinite;
	}

	@keyframes questions {
		0% {
			background-position: 0 0;
		}

		100% {
			background-position: 0 100%;
		}
	}
}

@media screen and (max-width: 550px) {
    .portfolioProjects {
        .container {
            margin-top: 5%;
            flex-direction: column;
            flex-wrap: nowrap;
            align-items: center;

            h4 {
                margin: 2.5% 0;
            }
        }
    }
}

@media screen and (max-width: 650px) {
    .portfolio {
        height: clamp(6.5rem, 0.5000rem + 20.0000vw, 13.5rem);
    }
}