body {
	background-color: rgb(245, 245, 245);
}

.wrapper {
	max-width: 1280px;
	width: 90%;
	margin: 0 auto;
}

.nextSection {
	display: flex;
	justify-content: center;
	width: 100%;
	position: absolute;
	bottom: 1rem;

	img {
		width: clamp(2rem, 1.3878rem + 2.0408vw, 4rem);
	}

	img:hover {
		animation: slide 1s infinite;
	}

	@keyframes slide {
		0% {
			transform: translateY(0);
		}

		100% {
			transform: translateY(25%);
		}
	}
}

.container,
ul {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}

ul {
	list-style-type: none;
	padding: 0;
}

a {
	text-decoration: none;
}

a:hover,
button:hover {
	cursor: pointer;
}

.homeAnchor:hover::after {
	content: "";
	width: 16%;
	height: 0.25rem;
	background: $primaryFontColor;
	position: absolute;
	bottom: 20%;
}

/* Dropdown Menu for Mobile */

.dropdown.homePage {
	position: absolute;
	right: 0rem;
	top: 5rem;
}

.dropdown.portfolioMenu,
.dropdown.contactMenu {
	position: absolute;
	right: 0rem;
	top: 2rem;
}

.dropButton {
	opacity: 0.8;
	position: relative;
	height: 3.3rem;
	border-top: 3px solid $primaryFontColor;
	border-bottom: 3px solid $primaryFontColor;
	border-left: 0;
	border-right: 0;
	font-weight: bold;
}

.dropdownContent {
	display: none;
	position: absolute;
	background-color: rgb(245, 245, 245);
	z-index: 1;
	padding-top: 1%;
	line-height: 3rem;
}

.dropdownContent a {
	text-decoration: none;
	border: 1px solid $primaryFontColor;
	display: block;
	text-align: center;
}

.dropButton,
.dropdownContent,
.dropdownContent a {
	width: clamp(6rem, 3rem + 10vw, 12rem);
}

.dropdownContent a:hover,
.dropdownContent a:focus {
	background-color: $primaryFontColor;
	color: white;
}

.dropdown:hover .dropdownContent,
.dropdown:focus .dropdownContent {
	display: block;
}

.dropdown:hover .dropButton,
.dropdown:focus .dropButton {
	background-color: $primaryFontColor;
	color: white;
}

footer {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding-bottom: 5%;
}

@media screen and (min-width: 901px) {
	.dropdown {
		display: none;
	}
}

@media screen and (max-width: 900px) {
	.dropdown {
		display: inline-block;
	}

	.navContainer ul {
		display: none;
	}
}

@media screen and(max-width: 450px) {
	nav {
		margin-top: 2%;
	}
}
