body {
	font-family: "Work Sans", sans-serif;
}

h1,
h2,
h3,
label,
footer {
	color: $primaryFontColor;
	font-weight: bold;
}

a {
	color: $primaryFontColor;
}

.mainHeading h1 {
	font-size: clamp(2.4rem, -1.3116rem + 8.8372vw, 10rem);
}

.portfolioHeading,
.contactHeading {
    font-size: clamp(3rem, 0.85714rem + 7.1428vw, 10rem);
}

h2 {
	font-size: clamp(2.4rem, 1.9408rem + 1.5306vw, 3.9rem);
}

h3,
p {
	font-size: clamp(1.4rem, 1.2776rem + 0.4082vw, 1.8rem);
}

.coreCompetencies h3 {
	font-size: clamp(1rem, 0.7551rem + 0.8163vw, 1.8rem);
}

.portfolioProjects h3 {
	font-size: clamp(2rem, 2.0612rem + -0.2041vw, 1.8rem);
}

.codeLinks {
	p {
		margin: 0;
		width: clamp(5rem, 3.4694rem + 5.1020vw, 10rem);
	}
}

.skipLink p {
	margin: 0;
}

.about p {
	line-height: 1.75;
}

.dropdownContent a {
    font-size: clamp(1.3rem, 1.05rem + 0.83333vw, 1.8rem);
}


nav {
	font-size: 2rem;
}

.mainHeading h3 {
	color: $secondaryFontColor;
}

.projects h3 {
	font-size: 2rem;
}

.fullTime {
	font-size: 1.6rem;
}

h4,
p {
	color: $secondaryFontColor;
}

h4 {
	font-weight: 600;
	font-size: 1.6rem;
}

.contactForm a {
	color: $secondaryFontColor;
}

.contactForm a:hover, .contactForm a:focus {
	outline: 2px solid $primaryFontColor;
}

input,
textarea,
label,
button {
	font-size: clamp(1.2rem, 1.0163rem + 0.6122vw, 1.8rem);
	color: black;
}